.autocomplete{
	position: relative;

	&-input{
		border: 1px solid getColor(hemelblauw darkest);

		&-field{
			&::-webkit-search-cancel-button {
				-webkit-appearance: none;
			}
			&::-ms-clear {
				display: none !important;
			}
		}
		display: flex;
		align-items: stretch;

		&-icon{
			width: 1.3rem;
			height: 1.3rem;
			fill: getColor(hemelblauw darkest);

			&-box{
				display: flex;
				align-items: center;
				background: white;
				border-right: none;
				padding-left: 0.6rem;
				padding-bottom: 0;

			}
		}

		&-clearInput{
			padding: {
				top: 0;
				right: 0.6rem;
				bottom: 0;
			}
			display: flex;
			align-items: center;
			background-color: white;
			border: 0;

			&-icon{
				width: 1.5rem;
				height: 1.5rem;
				fill: getColor(hemelblauw darkest);
				@include hoverEtc{
					fill: getColor(hemelblauw default);
					transition: fill 400ms;
				}
			}
		}
	}

	&-suggestions{
		width: 100%;
		position: absolute;
		background-color: white;
	    border-top: none;
	    @include zIndex(tabs);

	    &.ac-border{
			border: 1px solid getColor(hemelblauw darkest);
			border-top: none;

			.autocomplete-input-clearInput-icon{
				display: block;
			}
	    }

		&-list{
			@include noBullets;
		}

		&-item{
			padding: 0;
			padding-left: 1rem;
			font-size: 80%;

			&-title{
				width: 100%;
				margin: 0;
				display: block;
				color: getColor(hemelblauw darkest);
				line-height: 1;
			}

			&-subTitle{
				color: getColor(hemelblauw default);
			}

			&-link{
				display: flex;
				border: none;

				.media-text,
				.media-figure {
					padding-top: 0.5rem;
					padding-bottom: 0.5rem;
					display: flex;
					flex-wrap: wrap;
					align-items: center;
				}
			}

			button {
				@include cleanButton;
				width: 100%;
				display: flex;
				border: 0;
			}

			&-button {
				&-inner {
					width: 100%;
					padding-top: .75rem;
					padding-bottom: .5rem;
					display: flex;

					.media-figure {
						margin-left: auto;
						fill: getColor(hemelblauw darkest);
					}
				}
			}

			.search-suggestion-icon {
				width: 1.25rem;
			}

			.externalLinkIcon {
				display: none;
			}

			&:hover{
				//@include theme((background-color: background), '.ac');
				background-color: getColor(hemelblauw lighter);
			}
		}
	}
}