.checkBox{
	display: flex;
	position: relative;
	margin: {
		right: 0.5rem;
		bottom: 0.5rem;
	}
	&-label{
		display: flex;
		cursor: pointer;
		padding-bottom: 0;
		background: getColor(hemelblauw lighter);
		transition: background 700ms;

		&.selected{
			@include theme((background: background), '.filter');
		}
	}

	&-input{
		@include hoverEtc{
			left: -999em;
		}


		// Should not change color when focused
		&:hover {
			& + .checkBox-label {
				@include theme((background: background), '.filter');
			}
		}

		&:focus + .checkBox-label {
			@include focusStyle;
		}
	}

	&-text{
		padding: 0.5rem;
	}

	.close{
		//@include theme()
		@include theme((background: foreground), '.filter')
		width: 2.6rem;
		height: 2.6rem;

		&-icon{
			fill: white;
		}
	}
}