.accordion {
	margin-bottom: 2rem;
}

.acc {
	@include theme((background-color: background, border-top-color: foreground), '.accordion');
	border-top: 1px solid;
	margin: {
		left: negative($halfSpace);
		right: negative($halfSpace);
	}

	&.isOpened{
		background-color: white;

		@include theme((border-top-color: foreground), '.accordion');
	}

	&-openCloseIcon{
		svg{
			height: 1rem;
			width: 1rem;
		}
		cursor: pointer;
		transition: transform .5s;
		fill: getColor(hemelblauw default);

		.isOpened &{
			transform: rotate(135deg);
			fill: getColor(hemelblauw grijscontrast);
		}
	}

	&-openCloseAll{
		display: block;
		margin-bottom: 1rem;
		margin-left: auto;

		&-button{
			display: flex;
			align-items: center;
			fill: getColor(hemelblauw default);
			@include cleanButton;
			
			svg{
				fill: getColor(hemelblauw default);
				width: 1.5rem;
				height: 1.5rem;
			}

			&-text{
				margin-right: 10px;
			}

			@include hoverEtc{
				color: getColor(hemelblauw darkest);
				fill: getColor(hemelblauw darkest);
			}
		}
	}

	&-header{
		margin-top: 1rem;
		margin-bottom: 1rem;
		fill: getColor(hemelblauw default);
		@include hoverEtc{
			color: getColor(hemelblauw darkest);
			fill: getColor(hemelblauw darkest);
		}
	}

	&-titles{
		margin-top: 1rem;
		margin-bottom: 1rem;
		color: getColor(hemelblauw default);
		font-weight: normal;
		flex: 1 1 0px;

		@include hoverEtc{
			color: getColor(hemelblauw darkest);

			+ .acc-openCloseIcon {
				fill: getColor(hemelblauw darkest);
			}
		}
		transition: font-weight 1s;

		.isOpened &{
			font-weight: bold;
		}
	}

	&-title{
		margin-right: 0.5rem;
		margin-top: 0;
		margin-bottom: 0;

		.isOpened & {
			@include theme((color: foreground), '.accordion');
		}
	}

	&-content{
		&-body{
			// Make sure all kind of different half column blocks in accordions
			// are aligned correctly
			padding: {
				left: $halfSpace;
				right: $halfSpace;
			}
			.column--half {
				margin-top: 1rem;
				margin-bottom: 1rem;
			}
			.directRegelen-list {
				width: 100%;
			}
		}
		&-title{
			@include cleanButton;
			transition: background-color 1s;
			width: 100%;
			padding: {
				left: $halfSpace;
				right: $halfSpace;
			}

			@include hoverEtc{
				@include theme((background: background), '.accordion');
			}
		}
	}

	&-smoothOpen{
		padding-top: $quarterSpace;
		padding-bottom: $halfSpace;
	}

}