.articleOverview {
	&--grey {
		.articleOverview-acc-warning {
			color: getColor(grijs grijs6);
			font-style: italic;
		}
	}
	

	&-acc{
		&-title{
			color: getColor(hemelblauw darkest);
			font-weight: bold;
		}

		&-warning{
			color: getColor(hemelblauw default);
		}

		&-list{
			margin-left: 0;
			@include noBullets;
		}

		&-item{
			margin-bottom: 1rem;
			
			@include hoverEtc{
				.articleOverview-acc-title{
					text-decoration: underline;
				}
			}
		}
	}
}