.loadingIndicator {
	@include zIndex(dialog);
	position: fixed;
	overflow: hidden;
	left: 0;
	right: 0;
	top: 0;
	height: 0.3rem;

	&-text {
		@include accHide;
	}
	$duration: 3s;

	$bars: (
		getColor(violet default),
		getColor(mosgroen default),
		getColor(hemelblauw default),
	);

	$barCount: length($bars);

	@function getDelay($duration, $barCount, $i) {
		@return (($i - 1)*$duration/$barCount);
	}

	@keyframes loading {
		from {left: 50%; width: 0; z-index: 100;}
		#{100%/$barCount} {left: 0; width: 100%; z-index: 10;}
		to {left: 0; width: 100%;}
	}


	&-bars {
		height: 100%;
		position: relative;
		width: 100%;
		background-color: nth($bars, $barCount);
	}


	&-bar {
		display: block;
		position: absolute;
		width: 0;
		height: 100%;
		left: 50%;
		text-align: center;

		@for $i from 1 through $barCount {
			&:nth-child(#{$i}) {
				background-color: nth($bars, $i);
				animation: loading $duration linear getDelay($duration, $barCount, $i) infinite;
			}
		}
	}
}




