// Wordt nu gebruikt voor: Toptaken

.labelList {
	@include noBullets;
	@include setPropWithMq(greater, font-size, getFontSize(h3));
	margin: 1rem 0 1.5rem 0;
	display: flex;
	flex-wrap: wrap;
	align-items: flex-start;

	&-title {
		margin-top: 1rem;
		font-weight: bold;
	}

	&-item {
		margin: 0 0.5rem 0.5rem 0;
		padding: 0;
		display: inline-block;
	}

	&-link {
		@include noUnderline;
		padding: .2rem .75rem;
		display: inherit;
		line-height: 1.6;
		font-style: italic;
		background-color: rgba(getColor(wit), 0.9);
		color: getColor(hemelblauw darkest);
		transition: all 0.2s ease-in-out;
		@include theme((color: foregroundDarker), '.labelList');

		@include hoverEtc {
			color: getColor(hemelblauw default);
			transform: scale(1.03);
			@include theme((color: foregroundDarker), '.labelList');
		}
	}
	&-text {
		flex: 1 1 0;
		line-height: 1.3;
	}
}

.noDropDown { //TopTaken op homepage
	margin-top: 5rem;
}