.filter{
	///////////Onderstaande is voor wetswijzigingen
	&-dropDown{
		padding: 10px;
		-moz-appearance: none;
		appearance: none;
		background: none;
		position: relative;
		border: none;
		padding-right: 3.5rem;
		max-width: 100%;
		//Voor IE11:
		&::-ms-expand{
			display: none;
		}
	}

	&-accHide{
		@include accHide;
	}

	&-downIcon{
		svg{
			width: 2.5rem;
			height: 2rem;
			fill: getColor(wit);
		}
		position: absolute;
		display: flex;
		align-items: center;
		right: 0;
		top:0;
		bottom: 0;
		background-color: getColor(violet default);
	}

	&-bg{
		display: inline-block;
		background-color: getColor(grijs lightest);
		border: 1px solid;
		border-right: none;
		-webkit-appearance: none;
		position: relative;
		max-width: 100%;
		margin-top: 1rem;
		margin-bottom: 2rem;
	}

	&-selected{
		&-close{
			@include theme((background-color: foreground), '.filters');
			transition: all 0.1s;
			display: flex;
			align-items: center;
			overflow: hidden;

			&-icon{
				width: 2.5rem;
				height: 1.5rem;
				display: block;
				fill: getColor(wit);
			}

			@include hoverEtc{
				cursor: pointer;
				transform: scale(1.02);
				transition: all 0.1s;
				box-shadow: -1px 0 5px;
			}
		}
	}


	///////////Onderstaande is voor filter-component
	&-tabs{
		padding-top: 1rem;
		margin-bottom: 0;
		background: getColor(wit);

		&-bottom{
			display: flex;
			justify-content: flex-end;
			align-items: center;
		}

		&-close{
			display: flex;
			margin-left: auto;
			background: transparent;
		}
	}

	&-remove{
		margin: auto 1rem auto auto;
	}

	&Bar{
		margin-top: 1rem;
		align-items: flex-end;
	}

	&-events{
		margin-bottom: 1rem;
	}

	.close{
		&:hover{
			box-shadow: -1px 0 5px;
		}
	}

	&-openFilter{
		&Button{
			width: 100%;
			margin-bottom: 1rem;
			background-color: getColor(paars lightest);
		}
		&-text{
			font-weight: bold;
			color: getColor(paars default);
			text-align: left;
			padding-left: 1rem;
		}
	}

	&-icon{
		&-filter{
			width: 1.5rem;
			height: 1.5rem;
			position: relative;
			left: 0.2rem;
		}

		&-plus{
			width: 1.5rem;
			height: 1.5rem;
			fill: getColor(paars default);
		}
	}

	&-closeCard {
		position: fixed;
		left: 0;
		right: 0;
		bottom: 0;
		padding: $halfSpace 0 $halfSpace 0;
		background-color: getColor(wit);

		&-content {
			display: flex;
			align-items: flex-end;
			flex-direction: column;
		}
	}

	&-closeCardButton{
		border: none;
		background: getColor(paars default);
		font-weight: bold;
	}

	&Mobile{
		&-card{
			.acc{
				background: getColor(wit);

				&-openCloseIcon{
					fill: getColor(paars default);
				}
			}
		}
	}
}