/* sgl-icons op de homepage */

.sgl{
	margin-top: 1rem;
	margin-bottom: 1rem;
	
	@include mqLesserThan(mediumLarge){
		flex-direction: column;
	}

	&-item{

		.interfaceLink{
			font-weight: bold;

			@include mqBetween(smallMedium mediumLarge){
				width: 50%;
				padding-left: 1rem;
			}
			@include mqLesserThan(mediumLarge){
				.goIcon{
					order: 1;
				}
			}
			&-text{
				@include mqLesserThan(smallMedium){
					flex: initial;
				}
			}
		}

		.infoText{
			color: getColor(hemelblauw darkest);
			margin-bottom: 0.5em;

			@include mqLesserThan(mediumLarge){
				display: none;
			}
		}
		@include mqLesserThan(mediumLarge){
				display: flex;
				
				.column:nth-child(1) &, .column:nth-child(2) &{
					border-bottom: 1px solid getColor(grijs border);
					margin-bottom: 1rem;
				}
		}
		@include mqLesserThan(smallMedium){
			flex-direction: column;
		}
	}

	.column--third{
		@include mqLesserThan(mediumLarge){
				width: calc(100% - 2rem);
		}
	}

	&-title{
		display: flex;
		margin-bottom: 0.5rem;
		@include mqBetween(smallMedium mediumLarge){
			width: 50%;
			padding-right: 1rem;
		}
	}

	&-icon{
		width: 3rem;
		height: 3rem;
		fill: getColor(hemelblauw darkest);
	}

	&-name{
		margin: {
			top: 0.8rem;
			left: 1rem;
		}
	}
}