/* LINKS */

// Add underline
@mixin underline {
	text-decoration: none;
	border-bottom: 1px solid;
}

// Undo underline
@mixin noUnderline {
	text-decoration: none;
	border-bottom: none;
}

// Hide underline without changing the dimensions relative
// to an underlined link
@mixin hideUnderline {
	text-decoration: none;
	border-bottom: 1px solid transparent;
}

a {
	color: inherit;
	@include underline;
	transition: color, fill 400ms;

	&:hover,
	&:focus,
	&:active {
		color: getColor(hemelblauw grijscontrast);
		fill: getColor(hemelblauw grijscontrast);
	}

}

h2, h3, h4, h5, h6 {
	a {
		@include noUnderline;
		&:hover,
		&:focus,
		&:active {
			@include underline;
		}
	}
}


.interfaceLink {
	$lineHeight: 1.6;
    background: none;
    border: none;
    padding: 0;

    @include theme((color: foreground, fill: foreground), #{&}, true);

	@at-root {
		&,
		.interfaceLinkStatic {
			@include noUnderline;
			color: getColor(hemelblauw grijscontrast);
			fill: getColor(hemelblauw grijscontrast);
			line-height: $lineHeight;

			display: inline-flex;
		    align-items: flex-start;
		}
	}

	&--noFlex {
		display: inline-block;
	}

	&--block {
		display: block;
	}

	&--more {
		font-weight: bold;
	}

	&--white {
		fill: getColor(wit);
	}

	&--asText {
		color: getColor(grijs darkest);
	}

	&--add {
		margin-top: 0.75rem;
		font-weight: bold;
	}
	&--remove {
		margin-bottom: 1.2rem;
		font-weight: bold;
	}

	&-icon {
		width: 1rem;
		flex: 0 0 auto;
		margin-right: 0.9em;

		@include theme((fill: foreground), #{&}, true);

		&--dark {
			fill: getColor(hemelblauw darkest);
		}
	}

	&-text {
		flex: 1 1 auto;
		margin-right: 5px;
	}

	.underliner {
		@include hideUnderline;
		transition: border-bottom-color 200ms;
	}


	@include hoverEtc {
		.underliner {
			@include underline;
		}
	}
}

.link {
	.underliner {
		@include hideUnderline;
		transition: border-bottom-color 200ms;
	}

	@include hoverEtc {
		.underliner {
			@include underline;
		}
	}
}

.headingLink {
	$lineHeight: 1.2;

	width: 100%;
	line-height: $lineHeight;
	@include noUnderline;
	color: getColor(hemelblauw darkest);
	fill: getColor(hemelblauw darkest);
	display: inline-flex;
    align-items: center;
    position: relative;
    font-weight: bold;
    @include theme((fill: foregroundDarker), #{&}, true);

	.underliner {
		@include hideUnderline;
		transition: border-bottom-color 200ms;
	}

	@include hoverEtc {
		@include noUnderline;
		.underliner {
			@include underline;
		}
	}


	&-bigIcon {
		width: 2em;
		height: 2em;
		margin-right: 0.5em;
		flex: 0 0 auto;
		vertical-align: middle;
	}

	&-icon {
		flex: 0 0 auto;
		height: ($lineHeight * 1em);
		vertical-align: middle;
		margin-left: 0.3rem;
	}

	&-leadingIcon {
		flex: 0 0 auto;
		height: ($lineHeight * 1em);
		margin-right: 0.3em;
		vertical-align: middle;
	}

	&-text {
		flex: 0 1 auto;
		line-height: ($lineHeight * 1em);
	}

	.goIcon {
		position: relative;
		top: -1px;
	}
}

.homeBlock-icon{
	@include theme((fill: foreground), '.homeBlockMore');
	width: 1.6em;
	height: 1.6em;
	margin-right: 10px;
}

.externalLinkIcon {
	height: 0.9rem;
	width: 0.9rem;
	display: inline-block;
	vertical-align: top;
}

.linkAnkeiler {
	color: getColor(grijs darkest);

	@include hoverEtc {
		color: inherit;
	}
}

.linkBox {
	margin-bottom: 15px;
	padding: 10px;
	border: 1px solid #d9d9d9;
	font-size: 1.1em;

	@include mqGreaterThan(extraLarge) {
		width: 50%;
	}

	a {
		width: 100%;
		color: getColor(zwart);
	}

	p {
		margin: 0;
	}

	.goIcon {
		float: right;
		fill: getColor(zwart);
	}
}