/* HEADER */

// logo styling -> _header-logo.scss
// payOff styling -> _header-payOff.scss

.header {
	background-color: rgba(getColor(wit), 0.9);
	position: relative;
	flex: none;
}

.brandBarTop {
	background: getColor(hemelblauw default);
	min-height: 1.875rem;
	margin-left: 0;
	padding-left: 0;

	@include mqGreaterThan(small) {
		min-height: 2.94rem;
	}

	@include mqGreaterThan(large) {
		min-height: 3rem;
	}

	@include mqGreaterThan(extraExtraExtraLarge) {
		min-height: 3.8rem;
	}
}

.brandBarBottom{
	background: getColor(hemelblauw lighter);
	min-height: 0.6rem;
	padding-bottom: 6px;
	position: relative;

	@include insideMargin(1.8rem 1rem);

	.labelList-link{
		background-color: getColor(wit);
	}
}

.brandBarPageTitle{
	color: getColor(hemelblauw darkest);
	margin-bottom:0;
}

.header-heading {
	padding: {
		top: 0.9rem;
		bottom: 0.65rem;
	}
}