.embedContainer {
    width: 100%;
    height: 0;
    position: relative;
    padding-bottom: 56.25%;
    display: block;

    &-mediaWrapper {
        display: block;
        position: absolute;
        left: 0;
        top:0;
        bottom: 0;
        right: 0;
    }

    &-media {
        display: block;
    	width: 100%;
    	height: 100%;
    }
}