.directRegelen{

	&-link{
		border-bottom: none;

		@include hoverEtc{
			.directRegelen-block{
				background-color: getColor(paars lightest);
				transition: background-color 400ms;
			}
			.directRegelen-label{
				background-color: getColor(paars default);
				color: white;
			}
		}

		.gridContainer > & {
			margin-top: 1rem;
   			margin-bottom: 1rem;
		}
	}

	&-block{
		display: inline-block;
		border-top: 3px solid getColor(paars default);
		padding: 0.6rem;
		width: 100%;
	}

	&-content{
		display: flex;
		align-items: center;
		color: getColor(paars default);
		fill: getColor(paars default);
	}

	&-text{
		flex: 1 1 auto;
		font-weight: bold;
	}

	&-label{
		background-color: getColor(paars lightest);
		padding: {
			top: 0.3rem;
			left: 0.7rem;
			right: 0.7rem;
			bottom: 0.3rem;
		}
		border-radius: 0.2rem;
		line-height: 1.2;
		font-size: 90%;
	}

	&-icon{
		width: 2rem;
		height: 2rem;
		margin-left: 0.5rem;
		flex-shrink: 0;
		align-self: baseline;

		&-goIcon{
			width: 1.5rem;
			height: 1.5rem;
			margin-left: 0.5rem;
		}
	}
}

/////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
///////////// Purple transaction block on example page /example /////////////////////////////////////////////////////////////////////////////////////////////////////
.transaction{

	&-bigBlock{
		padding: 0.5rem;
		display: inline-block;
		background-color: getColor(paars default);
		fill: white;
		transition: width 1s ease-in-out;

		&-header{
			display: flex;
			align-items: center;
		}

		&-body{
			width: 100%;
			margin-left: 1rem;
			display: block;

			.gemeente-overzicht-search-box-input{
				width: 70%;
				margin-top: 0.5rem;
				@include mqLesserThan(mediumLarge){
					width: 100%;
				}
			}
		}

		&.closed{
			cursor: pointer;
			@include hoverEtc{
				background-color: darken(getColor(paars default), 7%);
				transition: background-color 400ms;
			}
		}

		&-closed{
			display: flex;
			text-align: left;
		}

		&.open{
			width: 100%;
		}

		&-open{
			display: flex;
			align-items: flex-start;
		}

		&-goIcon{
			margin-left: 1rem;
			display: flex;

			.goIcon{
				width: 2rem;
				height: 2rem;
			}
		}

		&-title{
			color: white;
			margin: 0;
		}

		&-subtitle{
			color: white;
		}

		&-selected{

			width: 100%;
			padding-top: 0.5rem;

			&-container{
				display: flex;
				align-items: flex-start;
			}

			&-gemeente{
				@include cleanButton;
				background-color: getColor(hemelblauw default);
				color: white;
				fill: white;
				padding: 0.3rem;
				font-size: getFontSize(small);
				margin-right: 1rem;
				@include mqBetween(smallMedium mediumLarge){
					width: 60%;
				}
				@include mqLesserThan(mediumLarge){
					margin-bottom: 0.7rem;
				}

				&-alternative{
					@include cleanButton;
					line-height: 1.4;
					font-size: getFontSize(small);
					color: white;
					flex: 1 1 auto;
					@include hoverEtc{
						text-decoration: underline;
						cursor: pointer;
					}
				}

				.media-text{
					padding-left: 0.5rem;
				}
			}

			&-gemeenteAndAlternative{
				display: flex;
				align-items: center;
				margin-top: 1rem;
				margin-bottom: 0.5rem;

				@include mqLesserThan(mediumLarge){
					display: block;
				}
			}

			&-remove{
				height: 1.5rem;
				width: 1.5rem;
				cursor: pointer;
				margin-left: 0.5rem;

				@include hoverEtc{
					transform: scale(1.1);
					transition: transform 400ms;
				}
			}
		}

		&-results{
			&-list{
				@include noBullets;

				.linkList-item{
					background-color: getColor(paars lightest);
					padding-left: 1rem;
					margin-bottom: 0.2rem;
					display: flex;
					.goIcon{
						fill: getColor(paars default);
					}
				}
			}

			&-nothing {
				background-color: getColor(paars lightest);
				padding: 0.3rem 0.3rem 0.3rem 1rem;
				margin-bottom: 0.2rem;

				&-zoekVerder{
					margin-left: 2.5rem;
					margin-top: 1rem;
				}

				.info{
					width: 1.5rem;
					height: 1.5rem;
					margin-right: 1rem;
				}
			}
		}

	}


}