@mixin sideContentWidth {
	width: calc(100vw - (#{$menuBarWidthSmall + 1rem});

	@include mqGreaterThan(small) {
		width: calc(100vw - #{$menuBarWidthLarge + 1rem});
	}
	@include mqGreaterThan(large) {
		width: calc(100vw - #{$menuBarWidthExtraLarge + 1rem});
	}

	@include mqGreaterThan(extraExtraLarge) {
		width: calc(100vw - #{$menuBarWidthExtraExtraLarge + 1rem});
	}

	@include mqGreaterThan(extraExtraExtraLarge) {
		width: 52rem;
	}
}


.is-menuOpened {
	overflow: hidden;
}

.container {
	width: 100%;
	min-height: 100vh;
}

.menuBarContainer {
	position: fixed;
	left: 0;
	top: 0;
	bottom: 0;
	// transition: transform 300ms ease-in-out;
	transition: transform 400ms cubic-bezier(0.02, 0.64, 0.15, 1.1);
	@include zIndex(menuBar);

	@include mqGreaterThan(large) {
		transform: translateX($menuBarWidthLarge - $menuBarWidthExtraLarge);
	}

	@include mqGreaterThan(extraExtraLarge) {
		transform: none;
	}

	@include mqGreaterThan(largest) {
		left: 50%;
		margin-left: negative(map-get($maxPageWidth, rem) / 2);
	}
}

.is-menuBarContainer-opened {
	transform: translateX(100vw) translateX(negative(#{$menuBarWidthSmall + 1rem}));

	@include mqGreaterThan(small) {
		transform: translateX(100vw) translateX(negative(#{$menuBarWidthLarge + 1rem}));
	}
	@include mqGreaterThan(large) {
		transform: translateX(100vw) translateX(negative(#{$menuBarWidthExtraLarge + 1rem}));
	}

	@include mqGreaterThan(extraExtraLarge) {
		transform: translateX(100vw) translateX(negative(#{$menuBarWidthExtraExtraLarge + 1rem}));
	}

	@include mqGreaterThan(extraExtraExtraLarge) {
		transform: translateX(52rem);
	}
}

.menuBar {
	position: relative;
	height: 100%;
	width: $menuBarWidthSmall;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	@include zIndex(menuBar bar);
	padding: {
		top: (2rem/16);
		bottom: (2rem/16);
	}

	@include mqGreaterThan(small) {
		width: $menuBarWidthLarge;
		padding: {
			top: (1rem/17);
			bottom: (1rem/17);
		}
	}

	@include mqGreaterThan(large) {
		width: $menuBarWidthExtraLarge;
		padding: {
			top: (1rem/18);
			bottom: (1rem/18);
		}
	}

	@include mqGreaterThan(extraExtraExtraLarge) {
		width: $menuBarWidthExtraExtraLarge;
	}

	@include before("") {
		$opacity: 0.9;
		$stop1: getColor(hemelblauw light);
		$stop2: mix($stop1, getColor(mintgroen default), 30%);
		position: absolute;
		left: 0;
		top: 0;
		bottom: 0;
		width: 100%;
		transition: width 0.4s;
		background: getColor(hemelblauw light);
		background: linear-gradient(rgba($stop1, $opacity), rgba($stop2, $opacity));
		}

	@include after("") {
		position: absolute;
		display: block;
		background: getColor(hemelblauw lighter);
		top: 0;
		bottom: 0;
		right: 100%;
		width: 2000px;
		width: 100vw;
		@include mqGreaterThan(extraExtraExtraLarge) {

			background: getColor(grijs lightest);
		}
	}

	&-context {
		margin-top: auto;
		transform: translateY($menuBarWidthSmall);
		transition: transform 0.2s;

		@include mqGreaterThan(small) {
			transform: translateY($menuBarWidthLarge);
		}

		&--showBackToTop {
			transform: none;
		}
	}

	&--title {
		@extend .h2;
	}
}

.menu-backgroundClose {
	position: fixed;
	display: block;
	left: 0;
	right: 0;
	bottom: 0;
	top: 0;
	z-index: getZIndex(overlay);
	@include cleanButton;
	@include hideText;
	background-color: rgba(getColor(hemelblauw darkest), 0.6);
	width: 100%;
	cursor: default;
}

.is-menuBar-retracted {
	@include mqLesserThan(small) {
		background: rgba(getColor(wit), 0.9);
		box-shadow: inset -1px 0 0 0 rgba(getColor(grijs light), 1);

		@include before("") {
			width: 0.25rem;
		}
		.menuBar-icon {
			fill: change-color(getColor(hemelblauw light), $lightness: 48%);
		}
	}
}

.menuBar-button {
	display: block;
	left: 0;
	background: none;
	border: none;
	width: $menuBarWidthSmall;
	height: $menuBarWidthSmall;
	position: relative;
	padding: ($menuIconSizePaddingRatioSmall * $menuBarWidthSmall);
	transition: background-color, color 200ms ease-in-out;
	color: getColor(hemelblauw darkest);
	overflow: hidden;

	margin: {
		top: 1.1rem;
		bottom: 1.1rem;
	}

	@include mqGreaterThan(small) {
		width: $menuBarWidthLarge;
		height: $menuBarWidthLarge;
		padding: ($menuIconSizePaddingRatioLarge * $menuBarWidthLarge);
		margin: {
			top: (10rem/17);
			bottom: (10rem/17);
		}
	}

	@include mqGreaterThan(large) {
		width: $menuBarWidthLarge;
		height: $menuBarWidthLarge;
		margin: {
			top: (12rem/18);
			bottom: (12rem/18);
		}
	}

	@include mqGreaterThan(extraExtraExtraLarge) {
		margin: {
			top: 0.9rem;
			bottom: 0.9rem;
		}
	}

	@include mqGreaterThan(large) {
		width: $menuBarWidthExtraLarge;
	}

	@include mqGreaterThan(extraExtraExtraLarge) {
		width: $menuBarWidthExtraExtraLarge;
	}

	@include hoverEtc {
		background-color: rgba(getColor(hemelblauw lighter), 0.7);
		color: inherit;
	}

	&.is-opened {
		background-color: getColor(hemelblauw lighter);

		.menuBar-icon {
			fill: getColor(zwart);
			transform: scale(1.1);
		}
	}

	&--context {

		&.is-opened {
			background-color: getColor(hemelblauw default);
			color: getColor(wit);

			.menuBar-icon {
				fill: getColor(wit);
			}
		}
	}

	&--toTop {
		background-color: rgba(getColor(hemelblauw lighter), 0.5);
		margin-bottom: 0;
	}

	&-inner {
		@include mqGreaterThan(large) {
			display: flex;
			flex-direction: row;
			align-items: center;
		}
	}

	&-text {
		position: relative;

		@include mqLesserThan(large) {
			@include accHide;
		}
		@include mqGreaterThan(large) {
			text-align: right;
			font-size: getFontSize(menuLabels);
			order: 1;
			flex: 1 1 auto;
			padding-right: ($menuIconSizePaddingRatioLarge * $menuBarWidthLarge);

		}
		@include mqGreaterThan(extraExtraLarge) {
			padding-right: $pad-half;
		}

	}
}

// SVG Icon
.menuBar-icon {
	width: $menuIconSizeRatioSmall * $menuBarWidthSmall;
	height: $menuIconSizeRatioSmall * $menuBarWidthSmall;
	fill: getColor(hemelblauw darkest);
	transition: all 0.2s ease-in-out;
	opacity: 0.9;

	@include mqGreaterThan(small) {
		width: $menuIconSizeRatioLarge * $menuBarWidthLarge;
		height: $menuIconSizeRatioLarge * $menuBarWidthLarge;
	}

	@include mqGreaterThan(large) {
		order: 2;
		flex: 0 0 auto;
	}

	.no-touch .menuBar-button:hover & {
		//stroke: getColor(wit);
		//stroke-width: 1px;
		//fill-opacity:0.1;
		//stroke-opacity:0.9;
		fill: getColor(zwart);
		//filter: url(../../images/svg-filters.svg#shadow);
		//fill: url("../../images/svg-filters.svg#gradient");
		transform: scale(1.1);
	}
}

.menuBar-sideContent {
	bottom: 0;
	z-index: 10;
	background: getColor(hemelblauw lighter);
	min-width: 15rem;
	height: 100%;
	@include zIndex(menuBar content);
	position: absolute;
	top: 0;
	right: 100%;

	@include sideContentWidth;
}

.menuBar-overlayContent {
	position: absolute;
	bottom: 0;
	left: 100%;
	background: rgba(getColor(hemelblauw default), .95);
	color: getColor(wit);
	width: 25rem;
	max-height: calc(100vh - 2rem);
	padding: 0.55rem 1rem;
	transform-origin: 0 100%;
	transform-origin: 0 calc(100% - 1.58rem);
	min-height: $menuBarWidthSmall;

	@include zIndex(menuBar contextContent);
	max-width: calc(100vw - (#{$menuBarWidthSmall} + 1rem);

	@include mqGreaterThan(small) {
		max-width: calc(100vw - #{$menuBarWidthLarge});
		min-height: $menuBarWidthLarge;
	}
	@include mqGreaterThan(large) {
		max-width: calc(100vw - #{$menuBarWidthExtraLarge});
	}

	@include mqGreaterThan(extraExtraExtraLarge) {
		max-width: calc(100vw - #{$menuBarWidthExtraExtraLarge});
	}

	.formField {
		display: block;
	}

	.formField-label {
		color: getColor(wit);
		text-align: left;
		width: 100%;
		margin: 0;
		padding-left: $halfSpace;
		padding-right: 3.5rem;
		padding-bottom: 0.5rem;

		@include mqLesserThan(smallMedium){
			padding-left: 0;
			margin-bottom: 0.5rem;
		}
	}

	&-wrapper {
		position: relative;
	}

	input {
		color: getColor(grijs darkest);
	}
}
.feedback {
	&-radio > p {
		margin-bottom: 0;
	}

	&-saved {
		padding-right: 3.5rem;

		&-title {
			margin-top: 0;

			color: inherit;
		}

		&-text {
			margin: 0;
		}
	}
	&-textField {
		margin-top: 1rem;

		.formField-label {
			@include accHide;
		}

		.formField-wrapper {
			display: block;
			width: calcColumnWidth(1/1);
		}

		.formField-optional {
			display: block;
			text-align: right;
		}

		.formField-inputWrapper {
			display: block;
			width: 100%;
		}
	}
}

.enqueteTitle{
	font-weight: bold;
	color: getColor(hemelblauw darkest);
	font-size: getFontSize(h3 all);
}

.enqueteQuestionIndicator{
	font-weight: bold;
}