.footer {
	/*
		Sticky footer is built according to
		https://github.com/philipwalton/solved-by-flexbox/blob/master/assets/css/components/site.css
	*/
	flex: none;
	background: getColor(hemelblauw lighter);
	@include insideMarginTop(0.5rem);
	font-size: getFontSize(small);

	@include mqGreaterThan(smallMedium) {
		font-size: getFontSize(root);
	}
}

.footerNav {
	&-link {
		color: getColor(hemelblauw darkest);
	}
}

.partnerLogos {
	text-align: center;

	&-intro {
		text-align: center;
	}
}

.partnerLogo {
	&-list {

	}

	&--color {
		filter: grayscale(100%);
		&:hover {
			filter: grayscale(0%);
		}
	}

	&--contentBottom { }
	&--contentRight { }

	&-item {
		position: relative;
		display: inline-block;
		width: auto;
	}
	&-link {
		display: block;
		border: none;
	}
	&-image {
		display: block;
		max-width: none;
		height: 1.8rem;
		width: auto;
		@include mqGreaterThan(large) {
			height: 2.5rem;
		}
		.sourceBlock & {
			//height: 3.5em;
		}
	}
	&--footer {
		opacity: 0.5;
		transition-property: opacity, transform;
		transition-duration: 200ms;
		transition-timing-function: ease-in-out;

		@include hoverEtc {
			transform: scale(1.1);
			opacity: 1;
		}
	}
}

.footerPartnersTitle {
	text-align: center;
}

.copyright {
	text-align: center;
	margin: 1rem 0;
}