.breadcrumbs {
	@include noBullets;
	margin: 0 1rem 0 0;
	padding-left: 0;
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	align-items: center;
	@include mqGreaterThan(small) {
		height: 3rem;
	}
	@include mqGreaterThan(extraExtraExtraLarge) {
		// Min-height not used because IE11 will fail to align items center.
		// See http://caniuse.com/#search=align-items, tab 'Known issues'
		height: #{4.4rem - 0.59rem};
	}
}

.breadcrumb {
	padding: 0;
	font-size: getFontSize(small);

	@include mqLesserThan(large) {
		// nth-child magic :)
		// if it is the first child and there are 2 or more children after
		&:first-child:nth-last-child(n + 3) {
			.breadcrumb-link {
				&:after {
					content: ">";
					display: inline-block;
					padding-left: $pad-quarter;
					color: getColor(wit);
				}
			}
			&:after {
				content: "…";
				display: inline-block;
				padding-right: $pad-half;
				color: getColor(wit);
			}
		}

		&:nth-child(n + 2) {
			display: none;
		}

		&:last-child {
			display: block;
		}
	}

	&-link {
		@include hideUnderline;
		display: inline-block;
		padding: $pad-half $pad-half $pad-half 0;
		color: getColor(wit);

		&:before {
			content: ">";
			display: inline-block;
			padding-right: $pad-quarter;
			color: getColor(wit);
		}
		&.breadcrumb-linkFirst {
			&:before {
				content: "";
				display: none;
			}
		}
	}
	&-text {
		@include hideUnderline;
	}

	@include hoverEtc {
		.breadcrumb-text {
			@include underline;
		}
	}
}