.linkList {
	@include noBullets;
	margin-left: 0;

	&--grey {
		.interfaceLink {
			color: getColor(grijs darkest);
			&:hover, &:active, &:focus {
				color: getColor(hemelblauw default);
			}
		}
	}

	&-item {
		break-inside: avoid;
	}

	&-itemMore {
		@include theme((color: foreground), '.listBlock');
		@include theme((color: foreground), '.homeBlock');

		.underliner {
			font-weight: bold;
		}
	}

	&-icon{
		flex: 0 0 auto;
		width: 1em;
		height: 1em;
		margin-top: 0 !important;
		margin-right: 1em;
	}

	&-title {
		margin-top: 0.5rem;
	}

	&-legalMods,
	&-internal{
		border-top: 3px solid getColor(hemelblauw default);
		margin-top: 1.5rem;
	}
}



%linkListWithTopBorder{
}

.subjectLinkList{

	margin-top: 2rem;
	margin-bottom: 1.5rem;
	order: 4;

	&.contact{
		margin-top: 1rem;
		@include mqLesserThan($twoColumnGridBreakpoint){
			margin-bottom: 0;
		}

	}

	&-header{

		margin-top: 0;

		& .interfaceLink{
			color: getColor(hemelblauw darkest);
			border-bottom: none !important;
			fill: getColor(hemelblauw darkest);

			@include hoverEtc{
				color: getColor(hemelblauw default);
				fill: getColor(hemelblauw default);
			}
		}
	}
}
