.date-display {
	&__container {
		margin-right: 1rem;
		position: relative;
	}

	&__icon {
		width: 4rem;
		height: 4rem;
	}

	&__date {
		width: 2.4rem;
		height: 2rem;
		background-color: getColor(wit);
		position: absolute;
		top: 1.7rem;
		left: .8rem;
		line-height: 1;
		font-weight: bold;
		font-family: "Arial Black", "Arial Bold", Arial, sans-serif;
		font-size: .5rem;
		text-align: center;
		@include theme((color: foreground), #{&}, true);

		&__day {
			display: block;
			font-size: 1rem;
		}
		&__month {
			display: block;
			font-size: 0.8rem;
		}
	}
}