$gutter: 3rem;
$mobileGutter: 1rem;
$space: 2rem;
$baseColor: getColor(hemelblauw default);
$buttonColor: getColor(violet default);

.search {
	&-scroll {
		display: flex;
		flex-direction: column;
		height: 100%;
	}
	&-header{
		flex: 0 0 auto;
		margin-top: 3.3rem;
		@include mqGreaterThan($twoColumnGridBreakpoint){
			margin-top: 4.8rem;
		}

		&-title {
			margin-top: 0;
		}
	}
	&-gutter{
		margin:{
			left: $gutter;
			right: $gutter;
		}

		@include mqLesserThan($twoColumnGridBreakpoint){
			margin: {
				left: $mobileGutter;
				right: $mobileGutter;
			}
		}
	}

	&-interface {
		&#{&} {
			position: relative;
			margin-bottom: 1rem;
			align-items: stretch;
			max-width: 40rem;
		}
	}

	&-input{
		padding-left: 1rem;
		border: 1px solid getColor(hemelblauw darkest);

		&::-webkit-search-cancel-button,
		&::-webkit-search-decoration {
    		-webkit-appearance: searchfield-cancel-button;
		}
		&::-ms-clear {
			display: block;
		}
	}

	&-clear-input{
		position: absolute;
		width: 1.5rem;
		height: 1.5rem;
		top: 11px;
		right: 18%;
		@include mqLesserThan($twoColumnGridBreakpoint){
			display: none;
		}
	}

	&-button{
		@include moreButton($buttonColor);
		width: auto;
	}

	&-suggestions{

		background-color: getColor(wit);
		border: 1px solid getColor(hemelblauw darkest);
		border-top: none;
		padding-left: 1rem;

		&-list{
			@include noBullets;
		}
	}

	&-suggestion{

		&-link{
			text-decoration: none;
			border: none;
			display: flex;
		}

		&-icon{
			transform: rotate(45deg);
		}
	}

	&-tabs{

		@include mqLesserThan($twoColumnGridBreakpoint){
			display: none;
		}

		&-item{
			@include cleanButton;
			width: 100%;
			line-height: 1.4;
		}

		&.tabs{
			margin-left: $gutter;
		}
	}

	&-tab{
		padding: 10px;
		background-color: $baseColor;
		border-bottom: 3px solid darken($baseColor, 7%);
		color: getColor(wit);
		transition: all .3s ease-in-out;

		&:hover{
			background-color: darken($baseColor, 7%);
		}

		.tab-link{
			//overwrite a-rules
			transition: none;
			//overwrite tab-link styles from ow-pagina

			&:before{
				position: relative;
				background: none;
			}
			&:hover{
				color: getColor(wit);
				fill: getColor(wit);
			}
		}

		&-active{

			&:hover{
				background-color: getColor(wit);
			}

			.tab-link{
				font-weight: bold;
				color: getColor(hemelblauw darkest);
				fill: getColor(hemelblauw darkest);
				background-color: getColor(wit);
				border-bottom:none;

				@include hoverEtc{
					color: getColor(hemelblauw darkest);
					fill: getColor(hemelblauw darkest);
				}

				&:before{
					position: relative;
					background: none;
				}
			}


		}
	}

	&-filters{

		@include mqLesserThan($twoColumnGridBreakpoint){
			background: white;
			border-bottom: 1px solid getColor(grijs border);
		}

		&-mobile{
			display: none;
			position: relative;
			margin-left: 1rem;
			margin-right: 1rem;

			$filterIconWidth: 2rem;
			@include mqLesserThan($twoColumnGridBreakpoint){
				display: flex;
				.filter-icon{
					width: $filterIconWidth;
					height: 1.5rem;
					position: absolute;
					top: 0;
					left: 0;
					bottom: 0;
					margin-top: auto;
					margin-bottom: auto;
				}

				.select-icon{
					width: 2rem;
					height: 2rem;
					position: absolute;
					top: 0;
					right: 0;
					bottom: 0;
					margin-top: auto;
					margin-bottom: auto;
					fill: getColor(hemelblauw darkest);
				}
			}

			&-select{
				width: 100%;
				font-weight: bold;
				color: getColor(hemelblauw default);
				padding:{
					top: 0.8rem;
					bottom: 0.8rem;
					left: $filterIconWidth;
				}
				appearance: none;
				background: none;
				border:none;
				position: relative;
			}

			&-option{
				color: getColor(hemelblauw darkest);
				&:checked{
					font-weight: bold;
				}
			}
		}
	}

	&-results {
		background-color: getColor(wit);
		padding-bottom: $gutter;
		position: relative;
		top: 0;
		flex: 1 1 0;
		overflow-y: auto;
		-webkit-overflow-scrolling: touch;

		&-list{
			transition-property: opacity;
			transition-duration: 200ms;
			list-style: none;
			padding-top: $space;
			padding-left: 0;

			li:before{
				content: "";
			}
		}

		&-message{
			padding-top: $space;
		}

		&-item{
			display: flex;

			&-link{
				display: block;
				margin-left: 1rem;
				border-bottom: 0;
				@include mqLesserThan(medium){
					margin-left: 0;
				}
			}

			&-icon{
				flex: 0 0 auto;
				svg{
					height: 2rem;
					width: 2rem;
					fill: getColor(hemelblauw darkest);
				}
				@include mqLesserThan(medium){
					display: none;
				}
			}

			&-title{
				margin-top: 0.5rem;
			}

			&-target{
				font-style: italic;
				display: block;
				color: $baseColor;
				word-break: break-all;
				word-break: break-word;
			}
		}

		&-more-button{
			margin-left: 5.6rem;
			margin-bottom: 2rem;
			background-color: $buttonColor;
			border: none;
			border-top: 3px solid darken($buttonColor, 7%);
			&:hover{
				background-color: darken($buttonColor, 7%);
			}
			@include mqLesserThan(medium){
				margin-left: 1rem;
			}
		}

		&-amount{
			margin:{
				top: 1rem;
			}

			min-height: 3rem;

			&-number{
				color: getColor(hemelblauw darkest);
			}

			@include mqLesserThan(medium){
				display: none;
			}
		}
	}

	&-facetSelectorIcon{
		width: 1rem;
		height: 1rem;
		display: none;

		.search-tab-active &{
			@include mqLesserThan(medium){
				display: block;
			}
		}

	}
}