.list {
	column-gap: $space;

	&--double {
		@include mqGreaterThan(small) {
			column-count: 2;
		}
	}

	&--doubleWhenMedium {
		@include setPropWithMq(greater, column-count, (
			smallMedium: 2,
			mediumLarge: 1
		));
	}

	&--triple {
		@include setPropWithMq(greater, column-count, (
			smallMedium: 2,
			mediumLarge: 3
		));
	}
	&--quadruple {
		@include setPropWithMq(greater, column-count, (
			small: 2,
			smallMedium: 3,
			mediumLarge: 4
		));
	}

	&-spacedItem {
		break-inside: avoid;
		margin: {
			left: $halfSpace;
			right: $halfSpace;
		}
	}

	&-item {
		break-inside: avoid;
	}
}

.singleLineList{
	display: flex;
	flex-wrap: nowrap;
	align-items: center;

	> div {
		margin-right: 1.5rem;
	}
}

.multiLineList{
	display: flex;
	flex-wrap: wrap;
}