.wettenEnRegels{

	&-header {
		margin-bottom: 2rem;
	}

	&-pageTitle{
		color: getColor(hemelblauw darkest);
	}

	&-anchorBlocks{

		background-color: getColor(hemelblauw lighter);
		margin-bottom: 2rem;

		.title{
			margin-top: 1rem;
			color: getColor(hemelblauw default);
		}
		.interfaceLink{
			color: getColor(hemelblauw darkest);
			fill: getColor(hemelblauw darkest);
		}

		.anchorBlock-linkList{
			line-height: 0.5;
			margin-bottom: 0.5rem;
		}
	}

	&-subjectAnchorBlock{
		background-color: getColor(hemelblauw lighter);
		border-right: 5px solid getColor(wit);
		@include mqLesserThan(mediumLarge){
			width: 100%;
			border-right: none;
		}
	}

	&-sectorAnchorBlock{
		background-color: getColor(hemelblauw lighter);
		@include mqLesserThan($threeColumnGridBreakpoint){
			width: calc(50% - 2rem);
		}
	}

	&-subjectLinkList{
		column-count: 2;
		margin-left: 0;

		@include mqLesserThan(mediumLarge){
			column-count: 1;
		}

		.linkList-item{
			padding: 0;
		}

		&-title{
			@include noUnderline;
			color: getColor(hemelblauw darkest);
			fill: getColor(hemelblauw darkest);
			font-weight: bold;
			line-height: 1.7;

			span{
				padding-right: 0.5rem;
			}
		}
	}

	&-overzichtListAndContact{
		display: flex;
	}

	&-overzichtList{
		@include mqLesserThan(mediumLarge){
			width: calc(50% - 2rem);
		}
	}

	&-overzichtLinkList{
		&-title{
			color: getColor(hemelblauw darkest);
			fill: getColor(hemelblauw darkest);
			font-weight: bold;
		}
	}

	&-legalMods{
		$baseClass: '.legalMods';
		@include theme((background-color: background), $baseClass, true);
		@include theme((border-top-color: foreground), $baseClass, true);
		border-top: 3px solid;

		.wettenEnRegels-content-mobile &{
			margin-top: 2rem;
		}

		&Page{

			.wettenEnRegels-pageTitle{
				color: getColor(hemelblauw default);
			}

			.wettenEnRegels-legalMods-app{
				margin-top: 0;
			}

			&-titleAndIntro{
				@include mqBetween($twoColumnGridBreakpoint $threeColumnGridBreakpoint){
					width: calcColumnWidth(1/2);
				}
			}

			&-app{
				&.appBottom{
					display: none;
				}
				@include mqBetween($twoColumnGridBreakpoint $threeColumnGridBreakpoint){
					width: calcColumnWidth(1/2);
				}
				@include mqLesserThan(smallMedium){
					&.appBottom{
						display: block;
					}
					&.appTop{
						display: none;
					}
				}
			}

			&-topTasks{
				position: relative;

				@include mqGreaterThan($threeColumnGridBreakpoint){
					@include after("") {
						position: absolute;
						top: 0;
						bottom: 0;
						left: 100%;
						border-right: $halfSpace solid getColor(wit);
					}
				}

				.labelList-link{
					background-color: getColor(violet default);
					color: getColor(wit);

					@include hoverEtc {
						color: white;
					}

				}
			}

			&-topTasksAndQuarterlies{
				@include mqLesserThan($threeColumnGridBreakpoint){
					background: getColor(violet lightest);
				}
				@include mqGreaterThan($threeColumnGridBreakpoint){
					background-image: linear-gradient(to right, getColor(violet lightest) 80%, transparent 20%);
				}
			}

			&-quarterlies{
				.interfaceLink{
					color: getColor(violet default);
					fill: getColor(violet default);
				}

				width: calcColumnWidth(1/1);

				@include mqGreaterThan($threeColumnGridBreakpoint){
					width: calcColumnWidth(1/3);
					padding-right: $halfSpace;
					background-color: getColor(violet lightest);
				}

				.wettenEnRegels-legalMods-list{
					@include mqBetween(smallMedium mediumLarge){
						column-count: 2;
					}
				}
			}

			&-filters{
				&-list{
					&-item{
						@include cleanButton;
						background-color: getColor(hemelblauw lighter);
						transition: background-color 0.75s;

						&-container{
							display: flex;
							align-items: stretch;
						}

						&-text{
							padding: 10px 15px 10px 15px;
						}

						@include hoverEtc {
							background-color: getColor(violet lightest);
						}

						&.filter-selected{
							background-color: getColor(violet light);
							& .filter-selected-close{
								display: flex;
								align-items: center;
							}
						}
					}

					margin-top: 1rem;

					&-tab{
						display: inline-block;
						.filter-selected-close{
							display: none;
						}
						margin-bottom: 0.5rem;
						margin-right: 0.5rem;
					}
				}
				&-title{
					margin: 0;
					margin-top: 1rem;
				}
			}

			&-wetswijzigingenList{

				&-title{
					@include mqLesserThan(mediumLarge){
						display: none;
					}
				}

				&-mobile{
					display: none;
					margin-top: 1rem;
				}
				@include mqLesserThan(mediumLarge){
					&-mobile{
						display: block;
					}
					&-desktop{
						display: none;
					}
				}
				&-moreButton{
					background-color: getColor(violet default);
					margin-bottom: 1rem;
					&-bottom{
						@include mqLesserThan(mediumLarge){
							display: none;
							margin-bottom: 2rem;
						}
					}
				}
			}

		}

		&-container{
			.wettenEnRegels-content &{
				margin:{
					top: 0.5rem;
					left: 0.5rem;
					right: 2rem;
					bottom: 0.5rem;
				}
			}
			margin-top: 1rem;
			.sideHeading{
					margin-top: 1rem;
				}
		}

		&-title{
			@include theme((color: foreground), $baseClass);
		}

		&-list{

			margin-top: 1rem;

			.linkList-item{
				padding: 0;
			}

			&-more{
				@include theme((color: foreground), $baseClass);
				@include theme((fill: foreground), $baseClass);
				font-weight: bold;
			}
		}

		&-mostViewed{
			@include theme((border-bottom-color: foreground), $baseClass);
			margin-bottom: 1rem;
		}

		&-app{

			border-top: 3px solid getColor(hemelblauw default);
			margin-top: 1rem;
			margin-bottom: 1rem;

			.wettenEnRegels-content-mobile &{
				margin-top: 2rem;
				margin-bottom: 0;
			}

			&-link{
				display: flex;
				margin-top: 1rem;
				border-bottom: none;
			}

			&-image{
				width: 65px;
				flex: 0 0 auto;
			}

			&-text{
				padding-left: 0.5rem;
				h3{
					margin-top: 0;
					color: getColor(hemelblauw default);
				}

				&-dl{
					font-size: 17px;
				}

			}

		}

		&-quarterlies{
			border-top: 1px solid;
			padding-top: 1rem;
			padding-bottom: 1rem;
			@include theme((border-top-color: foreground), $baseClass);

			.wettenEnRegels-legalModsPage &{
				border-top: none;
			}
		}
	}

	&-acc{
		&-subject{
			background-color: getColor(hemelblauw lightest);
			border-top: 2px solid getColor(hemelblauw darkest);
		}
		&-sector{
			background-color: getColor(hemelblauw lightest);
			border-top: 2px solid getColor(hemelblauw darkest);
		}

		.acc.isOpened {
			background-color: getColor(hemelblauw lighter);
		}
	}

	&-content{

		&-mobile{
			display: none;
		}

		@include mqLesserThan(mediumLarge){
			display: none;

			&-mobile{
				.blockContact{
					margin-top: 2rem;
				}
				display: block;
			}
		}

	}

	&-onderwerp{
		&-acc{
			&-title{
				color: getColor(hemelblauw darkest);
				font-weight: bold;
			}

			&-list{
				margin-left: 0;
			}

			&-item{
				margin-bottom: 1rem;
				@include hoverEtc{
					.wettenEnRegels-onderwerp-acc-title{
						text-decoration: underline;
					}
				}
			}
		}

		&-header{

			&-left{
				margin-top: 1rem;
				@include mqGreaterThan(mediumLarge) {
					width: calcColumnWidth(2/3);
				}

				.sectionTitle{
					margin: 0;
				}

				.contentTitle{
					margin-bottom: 0;
					@include mqGreaterThan(mediumLarge){
						margin-bottom: 2rem;
					}
				}
			}

			&-right{
				margin-top: 1rem;
				@include mqGreaterThan(mediumLarge) {
					width: calcColumnWidth(1/3);
				}

				.interfaceLink{
					color: getColor(hemelblauw darkest);
					fill: getColor(hemelblauw darkest);


					&:hover{
						color: getColor(hemelblauw default);
						fill:  getColor(hemelblauw default);
					}
				}

			}
		}

		.labelList-link{
			background-color: getColor(hemelblauw lighter);
		}
	}

	&-tab{
		&-list{
			&-item{

				margin-bottom: 1rem;

				&-title{
					color: getColor(hemelblauw darkest);
					font-weight: bold;
				}

				&-text{
					color: getColor(zwart);
				}

				&-subTitle{
					color: getColor(grijs grijs6);
					font-style: italic;
				}

				&:hover{
					.wettenEnRegels-tab-list-item-subTitle{
						color: getColor(hemelblauw default);
					}
					.wettenEnRegels-tab-list-item-text{
						color: getColor(hemelblauw default);
					}
				}
			}

			&-moreButton{
				@include moreButton(getColor(violet default));
				margin-bottom: 1rem;
				margin-top: 1rem;
				display: block;
			}
		}
	}
}