.blockContact {
	border-top: 3px solid getColor(paars default);
	padding-top: 0.5rem;
	padding-bottom: $space;

	&-header {
		padding-bottom: .5rem;
		border-bottom-width: 1px;
		border-bottom-style: solid;
		color: getColor(paars default);
	}

	&-title {
		margin-top: 0;
		margin-bottom: 0.25rem;
		padding-top: 0.5rem;
	}

	&-item {
		margin: 0;
		padding: 0.5rem 0;
		border-bottom: 1px solid getColor(paars default);
	}

	&-icon {
		width: 1em;
		height: 1em;
		margin-right: .75em;
		fill: getColor(paars default);
	}

	&-link {
		display: block;
	}

	.emphasis {
		color: getColor(paars default);
	}
}
